import moment from "moment";
import LineChart from "./LineChart.vue";
// import BarChart from "./BarChart.vue";

export default {
  components: {
    LineChart,
    // BarChart,
  },
  name: "Chart",
  props: {
    resultSet: Object,
    loading: Boolean,
    title: String,
    type: String,
  },
  methods: {
    dateFormatter: function (value) {
      return moment(value).format("MMM YY");
    },
  },
  computed: {
    values: function () {
      if (this.loading) return [];
      console.log("qqqqq77777q....", this.loading, this.title, this.resultSet);
      return this.resultSet ? this.resultSet.chartPivot() : [];
    },
    metrics: function () {
      if (this.loading) return [""];
      console.log("asaasss....", this.loading, this.title, this.type);
      return this.resultSet
        ? this.resultSet.seriesNames().map((x) => x.key)
        : [];
    },
  },
  data() {
    return {
      colors: [
        "#7DB3FF",
        "#49457B",
        "#FF7C78",
        "#FED3D0",
        "#6F76D9",
        "#9ADFB4",
        "#2E7987",
      ],
    };
  },
};
